/* noto-sans-kr-100 - latin_korean */
@font-face {
  font-family: 'Noto Sans KR';
  font-style: normal;
  font-weight: 100;
  src: url('../../assets/fonts/noto-sans-kr-v8-latin_korean-100.eot'); /* IE9 Compat Modes */
  src: local('SourceHanSans-Light'), local('Noto Sans KR Thin'), local('NotoSansKR-Thin'),
       url('../../assets/fonts/noto-sans-kr-v8-latin_korean-100.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../../assets/fonts/noto-sans-kr-v8-latin_korean-100.woff2') format('woff2'), /* Super Modern Browsers */
       url('../../assets/fonts/noto-sans-kr-v8-latin_korean-100.woff') format('woff'), /* Modern Browsers */
       url('../../assets/fonts/noto-sans-kr-v8-latin_korean-100.svg#NotoSansKR') format('svg'); /* Legacy iOS */
}

