* {
  font-family: 'Noto Sans KR', sans-serif;
  margin: 0;
  padding: 0;
  word-break: keep-all;
  font-feature-settings: normal;
  text-rendering: optimizeLegibility;
}

body {
  margin: auto;
  padding: 20px 30px 50px 30px;
}

a {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

#top-container {
  background-color: white;
  position: fixed;
  width: 190px;
  padding-right: 5px;
  padding-bottom: 5px;
  z-index: 999;

  h1 {
    font-size: 24px;
    display: inline;
    font-weight: 900;
  }

  small {
    display: block;
    font-size: 12px;
    font-weight: 100;
  }

  .logo-link {
    span {
      font-size: 14px;
      font-weight: 400; 
    }

    &:hover {
      color: white;
      text-shadow: 1px 1px 0 black, -1px 1px 0 black, -1px -1px 0 black, 1px -1px 0 black;
    }
  }

  .info-lnk:hover {
    color: white;
    background-color: black;
  }
}

#main-container {
  margin-top: 90px;
  margin-bottom: 50px;
}